.accordion-li{list-style:none;margin:0;padding:0;background-color:rgba(0,0,0,0);max-width:30rem;border-radius:.4rem;overflow:hidden}.accordion-li__menu+.accordion-li__menu{border-top:1px solid rgba(0,0,0,.1)}.accordion-item__menu{text-decoration:none;margin:0;font-size:22px;color:#d8e8ff;font-weight:300;margin-top:30px;position:relative}.accordion-item__content_menu{padding:0}.accordion-item__content_menu ul{list-style-type:none;padding-left:20px}.accordion-item__content_menu ul li{margin-top:30px}.accordion-item__content_menu ul li:hover a{color:#f1c2c7}.accordion-menu--opened .accordion-menu__icon{transform:rotate(180deg)}.accordion-menu--opened .accordion-menu__inner{max-height:100rem;transition-timing-function:cubic-bezier(0.895, 0.03, 0.685, 0.22);transition-duration:.5s;transition-property:max-height}.accordion-menu--opened .accordion-menu__content{opacity:1;transform:translateY(0);transition-delay:.2s;transition-timing-function:ease-in-out;transition-duration:.2s;transition-property:opacity,transform}.accordion-menu__line{display:block;padding:.8rem 1.2rem;display:flex;justify-content:space-between;align-items:center;background-color:rgba(0,0,0,0);z-index:2;position:relative}.accordion-menu__title{font-size:30px;margin:0;font-weight:700;color:#f1c2c7;position:relative;font-family:"Sansation"}.accordion-menu__title::after{position:absolute;content:"";color:#f1c2c7;font-size:40px;font-weight:bold;right:-25px;top:-10px;transform:rotate(-90deg)}.accordion-menu__icon{width:1.2rem;height:1.2rem;transition:transform .3s ease-in-out;background-size:contain;background-repeat:no-repeat;background-image:url(data:image/png;base64,);opacity:.6}.accordion-menu__inner{max-height:0;overflow:hidden;text-transform:cubic-bezier(0.95, 0.05, 0.795, 0.035);transition-duration:.5s;transition-property:max-height;z-index:1;position:relative}.accordion-menu__content{opacity:0;transform:translateY(-1rem);transition-timing-function:linear,ease;transition-duration:.1s;transition-property