.accordion-list{list-style:none;margin:0;padding:0;background-color:rgba(0,0,0,0);max-width:30rem;border-radius:.4rem;overflow:hidden}.accordion-list__item+.accordion-list__item{border-top:1px solid rgba(0,0,0,.1)}.accordion-item--opened .accordion-item__icon{transform:rotate(180deg)}.accordion-item--opened .accordion-item__inner{max-height:100rem;transition-timing-function:cubic-bezier(0.895, 0.03, 0.685, 0.22);transition-duration:.5s;transition-property:max-height}.accordion-item--opened .accordion-item__content{opacity:1;transform:translateY(0);transition-delay:.2s;transition-timing-function:ease-in-out;transition-duration:.2s;transition-property:opacity,transform}.accordion-item__line{display:block;padding:.8rem 1.2rem;display:flex;justify-content:space-between;align-items:center;background-color:rgba(0,0,0,0);z-index:2;position:relative}.accordion-item__title{font-size:30px;margin:0;font-weight:700;color:#f1c2c7;position:relative;font-family:"Sansation"}.accordion-item__title::after{position:absolute;content:"";color:#f1c2c7;font-size:40px;font-weight:bold;right:-25px;top:-10px;transform:rotate(-90deg)}.accordion-item__icon{width:1.2rem;height:1.2rem;transition:transform .3s ease-in-out;background-size:contain;background-repeat:no-repeat;background-image:url(data:image/png;base64,);opacity:.6}.accordion-item__inner{max-height:0;overflow:hidden;text-transform:cubic-bezier(0.95, 0.05, 0.795, 0.035);transition-duration:.5s;transition-property:max-height;z-index:1;position:relative}.accordion-item__content{opacity:0;transform:translateY(-1rem);transition-timing-function:linear,ease;transition-duration:.1s;transition-property:opacity,transform;transition-delay:.5s;padding:0 50px 1.2rem}.accordion-item__paragraph{margin:0;font-size:17px;color:#d8e8ff;font-weight:300;margin-top:30px;position:relative}.accordion-item__paragraph::before{content:"";position:absolute;width:15px;height:15px;background:url(./Images/location.svg);background-repeat:no-repeat;left:-30px;top:6px}.accordion-item__phone_num{margin:0;font-size:17px;color:#d8e8ff;font-weight:300;margin-top:30px;position:relative}.accordion-item__phone_num::before{content:"";position:absolute;width:15px;height:15px;background:url(./Images/mail.svg);background-repeat:no-repeat;left:-30px;top:5px}.accordion-item__mail{margin:0;font-size:17px;color:#d8e8ff;font-weight:300;margin-top:30px;position:relative}.accordion-item__mail::before{content:"";position:absolute;width:15px;height:15px;background:url(./Images/mail.svg);background-repeat:no-repeat;left:-30px;top:5px}@media(min-width: 768px){.accordion-item__paragraph{font-size:15px}.accordion-item__phone_num{font-size:15px}.accordion-item__mail{font-size:15px}.accordion-item__content_menu ul{margin-top:30px}.accordion-item__content{padding-right:0}.accordion-item__title{font-size:20px}.accordion-list{width:230px}}@media(min-width: 992px){.accordion-list{width:unset}}@media(min-width: 1200px){.accordion-item__title{font-size:30px}.accordion-item__paragraph{font-size:17px}.accordion-item__phone_num{font-size:17px;margin-top:40px}.accordion-item__mail{font-size:17px;margin-top:40px}}